/*--------------------------------------------------------------
# Page
--------------------------------------------------------------*/
html,
body {
  width: 100%;
  height: 100%; }

.site {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch; }

.site-main {
  flex-grow: 1; }

.site-header,
.site-main,
.site-footer {
  flex-shrink: 0; }

/*--------------------------------------------------------------
# Grid
--------------------------------------------------------------*/
.wrap {
  position: relative;
  max-width: 1080px;
  padding: 0 12px;
  margin: 0 auto;
  width: 100%; }
  .wrap--content {
    max-width: 688px; }
  .wrap--mid {
    max-width: 880px; }

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  background-color: #FFFFFF;
  color: #63666A; }

.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */ }
  .screen-reader-text:focus {
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    clip-path: none;
    display: block;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

h1, h2, h3, h4, h5, h6 {
  color: #000000;
  font-weight: 700;
  line-height: 1.3; }
  h1 a:hover, h2 a:hover, h3 a:hover, h4 a:hover, h5 a:hover, h6 a:hover, h1 a:active, h2 a:active, h3 a:active, h4 a:active, h5 a:active, h6 a:active, h1 a:focus, h2 a:focus, h3 a:focus, h4 a:focus, h5 a:focus, h6 a:focus {
    color: #4196b9; }

/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.site-header > div {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9999;
  box-shadow: 0 1px 20px rgba(0, 0, 0, 0.5);
  transition: all .2s; }
  .header-collapsed .site-header > div {
    top: -36px; }

.site-header .bar {
  height: 72px;
  background-color: #FFFFFF; }
  .site-header .bar > .wrap {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .site-header .bar .branding h1 {
    font-size: 17px;
    text-transform: uppercase; }
  .site-header .bar .branding p {
    font-size: 13px;
    text-transform: uppercase;
    font-weight: 300; }

.site-header--bg-img {
  height: 75vh;
  background-size: cover;
  background-position: center; }

.site-header .offcanvas-toggle {
  position: relative;
  width: 48px;
  height: 48px; }
  @media (min-width: 61.25em) {
    .site-header .offcanvas-toggle {
      display: none; } }
  .site-header .offcanvas-toggle i {
    position: absolute;
    width: 24px;
    height: 2px;
    left: calc(50% - 12px);
    top: calc( 50% - 1px);
    transition: all .2s;
    background-color: #63666A; }
    .site-header .offcanvas-toggle i:nth-of-type(1) {
      margin-top: -8px; }
    .site-header .offcanvas-toggle i:nth-of-type(3) {
      margin-top: 8px; }
  .offcanvas-open .site-header .offcanvas-toggle i:nth-of-type(1) {
    margin-top: 0;
    transform: rotate(45deg); }
  .offcanvas-open .site-header .offcanvas-toggle i:nth-of-type(2) {
    opacity: 0; }
  .offcanvas-open .site-header .offcanvas-toggle i:nth-of-type(3) {
    margin-top: 0;
    transform: rotate(-45deg); }

/*--------------------------------------------------------------
# Site Nav
--------------------------------------------------------------*/
.site-nav li.active {
  color: #000000;
  font-weight: 700; }
  .site-nav li.active a {
    opacity: 1; }

.site-nav a {
  opacity: .8; }
  .site-nav a:hover, .site-nav a:active, .site-nav a:focus {
    opacity: 1; }

.site-header .site-nav {
  display: none; }
  @media (min-width: 61.25em) {
    .site-header .site-nav {
      display: block;
      font-size: 15px; }
      .site-header .site-nav > ul {
        display: flex; }
      .site-header .site-nav a {
        display: block;
        padding: 12px; } }

.offcanvas .site-nav {
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6; }
  .offcanvas .site-nav li:not(:last-child) {
    border-bottom: 1px solid #e6e6e6; }
  .offcanvas .site-nav a {
    display: block;
    padding: 12px 6px; }

/*--------------------------------------------------------------
# Section
--------------------------------------------------------------*/
.section {
  padding: 48px 0; }
  .section .section__header {
    text-align: center;
    margin-bottom: 36px; }
    .section .section__header h2 {
      font-size: 30px; }
    .section .section__header p {
      margin-top: 12px; }
      .section .section__header p a {
        color: #4196b9; }
        .section .section__header p a:hover, .section .section__header p a:active, .section .section__header p a:focus {
          color: #3a87a6; }
  .section--bg {
    background-color: #f2f2f2; }

/*--------------------------------------------------------------
# List
--------------------------------------------------------------*/
.list--teasers {
  display: grid;
  grid-gap: 48px; }
  @media (min-width: 46.25em) {
    .list--teasers {
      grid-template-columns: repeat(3, 1fr); } }

.list--portfolio {
  display: block; }
  @media (min-width: 46.25em) {
    .list--portfolio > li {
      display: flex;
      flex-flow: row wrap; }
      .list--portfolio > li:not(:last-child) {
        margin-bottom: 24px; } }

/*--------------------------------------------------------------
# Teaser
--------------------------------------------------------------*/
.teaser img {
  margin-bottom: 18px; }

.teaser .topic {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 13px; }
  .teaser .topic:hover, .teaser .topic:active, .teaser .topic:focus {
    color: #4196b9; }

.teaser h2 {
  font-size: 24px;
  margin-bottom: 12px; }

.teaser p {
  font-size: 95%; }

.teaser--portfolio {
  position: relative; }
  .teaser--portfolio:not(:last-child) {
    margin: 0 0 48px; }
    @media (min-width: 46.25em) {
      .teaser--portfolio:not(:last-child) {
        margin: 0 24px 0 0; } }
  .teaser--portfolio--2x3 {
    flex: .666667 0%; }
  .teaser--portfolio--3x2 {
    flex: 1.5 0%; }
  .teaser--portfolio img {
    margin-bottom: 0;
    transition: all .2s; }
  .teaser--portfolio h2 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    width: 100%;
    opacity: 0;
    color: #FFFFFF;
    transition: all .2s;
    font-weight: 300;
    font-size: 17px;
    letter-spacing: .2px; }
  .teaser--portfolio:hover h2, .teaser--portfolio:active h2, .teaser--portfolio:focus h2 {
    opacity: 1; }
  .teaser--portfolio:hover img, .teaser--portfolio:active img, .teaser--portfolio:focus img {
    opacity: .8; }

/*--------------------------------------------------------------
# Post
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Cta
--------------------------------------------------------------*/
.cta--thin {
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #9cbdcc;
  color: #FFFFFF; }
  .cta--thin a {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
    padding: 0 6px; }

/*--------------------------------------------------------------
# Socials
--------------------------------------------------------------*/
.socials {
  display: flex; }
  .socials > li:not(:last-child) {
    margin-right: 12px; }
  .socials a {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px; }
    .socials a[href*='facebook'] {
      background-color: #4267B2; }
    .socials a[href*='twitter'] {
      background-color: #1DA1F2; }
    .socials a[href*='instagram'] {
      background-color: #833AB4; }
    .socials a[href*='youtube'] {
      background-color: #FF0000; }
    .socials a[href*='500px'] {
      background-color: #000000; }
    .socials a:hover, .socials a:active, .socials a:focus {
      opacity: .8; }
  .site-footer .socials {
    justify-content: center; }
    .site-footer .socials a {
      background-color: gray; }
      .site-footer .socials a:hover, .site-footer .socials a:active, .site-footer .socials a:focus {
        opacity: 1; }
        .site-footer .socials a:hover[href*='facebook'], .site-footer .socials a:active[href*='facebook'], .site-footer .socials a:focus[href*='facebook'] {
          background-color: #4267B2; }
        .site-footer .socials a:hover[href*='twitter'], .site-footer .socials a:active[href*='twitter'], .site-footer .socials a:focus[href*='twitter'] {
          background-color: #1DA1F2; }
        .site-footer .socials a:hover[href*='instagram'], .site-footer .socials a:active[href*='instagram'], .site-footer .socials a:focus[href*='instagram'] {
          background-color: #833AB4; }
        .site-footer .socials a:hover[href*='youtube'], .site-footer .socials a:active[href*='youtube'], .site-footer .socials a:focus[href*='youtube'] {
          background-color: #FF0000; }
        .site-footer .socials a:hover[href*='500px'], .site-footer .socials a:active[href*='500px'], .site-footer .socials a:focus[href*='500px'] {
          background-color: #000000; }

/*--------------------------------------------------------------
# Offcanvas
--------------------------------------------------------------*/
.offcanvas {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 100%;
  z-index: 999;
  padding: 132px 12px 0;
  background-color: #FFFFFF;
  transition: all .2s; }
  .offcanvas-open .offcanvas {
    left: 0; }
  .header-collapsed .offcanvas {
    padding-top: 96px; }

body.offcanvas-open {
  overflow: hidden; }

/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
.site-footer {
  background-color: #333333;
  color: #FFFFFF; }
  .site-footer .site-footer__top {
    padding: 48px 0;
    background-color: #4d4d4d; }
  .site-footer small {
    opacity: .7;
    display: block;
    padding: 12px 0;
    text-align: center; }
